"use strict";

import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { fadeInAnimation } from "../../../core/route-animation/route.animation";
import { Entreprise } from "../metier/entreprise.metier";
import { BaseComponent } from "../../../core/component/base-component";
import { EntrepriseService } from "../service/entreprise.service";
import { PageTitleService } from "../../../core/page-title/page-title.service";
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { VilleService } from '../../ville/service/ville.service';
import { Ville } from '../../ville/metier/ville.metier';

@Component({
  selector: 'ms-entreprise-list',
  templateUrl: './entreprise.list.component.html',
  styleUrls: ['./entreprise.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class EntrepriseListComponent extends BaseComponent implements OnInit {

  public entrepriseForm: FormGroup;
  private entreprise: Entreprise;
  private villeList: Array<Ville>;

  constructor(private fb: FormBuilder, private entrepriseService: EntrepriseService,
    private pageTitleService: PageTitleService,
    private villeService: VilleService) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("ENTREPRISE");

    this.entrepriseForm = this.fb.group({
      id: '',
      nom: ['', Validators.required],
      responsable: ['', Validators.required],
      type: ['', Validators.required],
      capital: ['', Validators.required],
      registreCommerce: ['', Validators.required],
      identifiantFiscale: ['', Validators.required],
      patente: ['', Validators.required],
      cnss: ['', Validators.required],
      ice: ['', Validators.required],
      ville: [null, Validators.required],
      adresse: ''
    });
    this.loadEntreprise();
  }

  private loadEntreprise() {
    this.entrepriseService.getEntreprise()
      .subscribe(entreprise => {
        this.entreprise = entreprise;
        this.entrepriseForm.patchValue(this.entreprise);
        this.loadVilleList();
        this.photo = entreprise.photo;
        if (this.photo === null) {
          this.photo = this.DEFAULT_PHOTO;
        }

      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadVilleList() {
    this.villeService.getVilleList()
      .subscribe(villeList => {
        this.villeList = villeList;
        this.entreprise.ville = this.villeList.find(ville => ville.id === this.entreprise.ville.id);
        this.entrepriseForm.patchValue({ ville: this.entreprise.ville }, { onlySelf: true });
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  saveEntreprise(entrepriseForm: any) {
    this.entreprise = new Entreprise(entrepriseForm);

    this.entreprise.photo = this.photo;
    if (this.entreprise.photo === this.DEFAULT_PHOTO) {
      this.entreprise.photo = null;
    }

    this.entrepriseService.saveEntreprise(this.entreprise)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/entreprise/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
      ;
  }

}






