import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {EntrepriseRoutes} from './entreprise.routing';
import {EntrepriseService} from "./service/entreprise.service";
import {EntrepriseListComponent} from "./entreprise.list/entreprise.list.component";
import {DataTableModule} from "angular2-datatable";
import { VilleService } from '../ville/service/ville.service';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    RouterModule.forChild(EntrepriseRoutes)
  ],
  declarations: [
    EntrepriseListComponent,
  ],
  providers:[
    EntrepriseService,
    VilleService
  ],
})

export class EntrepriseModule {
}



