import {Routes} from '@angular/router';

import {EntrepriseListComponent} from "./entreprise.list/entreprise.list.component";

export const EntrepriseRoutes: Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [
    {path: 'list', component: EntrepriseListComponent},
  ]
}];



