import { Ville } from "../../ville/metier/ville.metier";

export class Entreprise {
  id: number;
  nom: string;
  responsable: string;
  type: string;
  capital: number;
  registreCommerce: string;
  identifiantFiscale: string;
  patente: string;
  cnss: string;
  ice: string;
  adresse: string;
  ville: Ville;
  photo: string;

  constructor(values: Object = {}) {
    Object.assign(this, values);
  }
}



